body {
  width: 5760px;
  overflow-y: hidden;
  overflow-x: hidden;
  background-color: #1f262a;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.panel {
  float: left;
  z-index: 10;
  min-height: 100vh;
  height: 100vh;
}

.navDiv {
  position: fixed;
  right: 94.48vw;
  z-index: 105;
  width: 100vh;
  height: 5.555555vw;
  transform: rotate(270deg);
  transform-origin: right bottom;
  top: -5.55vw;
  background-color: #1f262a;
}
.backup {
  right: 1360px;
}

ul#navbar {
  background-color: #1f262a;
  text-align: center;
}

ul#navbar li {
  display: inline-block;

  padding: 18px;
  color: white;
}

ul#navbar li a {
  text-decoration: none;
  color: white;
}

ul#navbar li a:hover {
  text-decoration: none;
  color: #c9a3b8;
}

.strikeout {
  font-size: 1.5em;
  line-height: 1em;
  position: relative;
}
.strikeout::after {
  border-bottom: 0.1em solid#c9a3b8;
  content: "";
  left: 0;
  margin-top: calc(0.125em / 2 * -1);
  position: absolute;
  z-index: 10;
  right: 0;
  top: 50%;
}

.fill {
  height: 100%;
  min-height: 100%;
}

.aboutText {
  color: white;
}
.nextText {
  color: #c9a3b8;
  position: absolute;
  right: 10px;
  transition: 1s;
}
.nextText:hover {
  color: #c9a3b8;
  position: absolute;
  right: 0px;
}

.divider {
  height: 100vh;
  position: relative;
}

.divider p {
  font-size: 30px;
  font-weight: bold;
  color: black;
  transform: rotate(270deg);
  position: absolute;
  z-index: 10;
  top: 60vh;
  display: inline-block;
  padding-bottom: 155px;
  width: 200px;
}

.z {
  z-index: 105;
}

.container.gallery-container {
  background-color: #fff;
  color: #35373a;
  min-height: 100vh;
  padding: 30px 50px;
}

.gallery-container p.page-description {
  text-align: center;
  margin: 25px auto;
  font-size: 18px;
  color: #999;
}

/* Override bootstrap column paddings */
.tz-gallery .row > div {
  padding: 2px;
  height: 50vh;
}

.tz-gallery img {
  width: 100%;
  border-radius: 0;
  position: relative;
  height: 49.5vh;
}

.tz-gallery .lightbox:before {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -13px;
  margin-left: -13px;
  opacity: 0;
  color: #fff;
  font-size: 26px;
  pointer-events: none;
  transition: 0.4s;
}

.tz-gallery .lightbox:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: black;
  content: "";
  transition: 1s;
  color: white;
}
.tz-gallery .lightbox:hover:after,
.tz-gallery .lightbox:hover:before {
  opacity: 0.9;
}

.tz-gallery .lightbox:hover #projectText {
  opacity: 1;
}

.tz-gallery .lightbox #projectText {
  z-index: 199999;
  opacity: 0;
  transform: translate(-50%, -50%);
  text-align: center;
  transition: 1s;
}

.projectHead {
  position: absolute;
  top: 21%;
  left: 50%;
  font-weight: bold;
  font-size: 18px;
  color: White;
}
.projectBody {
  position: absolute;
  top: 65%;
  left: 50%;
  color: White;
}
.projectSub {
  position: absolute;
  top: 36%;
  left: 50%;
  font-size: 16px;
  color: #c9a3b8;
  transition: 1.5s;
}

@media (max-width: 768px) {
  body {
    padding: 0;
  }
}

.emptyProject {
  position: absolute;
  top: 25%;
  left: 38%;
  font-weight: bold;
  font-size: 100px;
  color: White;
}

